import React, { useState, useEffect, useContext } from 'react';
import { getProducts } from '../services/api';
import { SocketContext } from '../context/SocketContext';

export default function ClientDashboard() {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedProduct, setSelectedProduct] = useState(null);
  const [status, setStatus] = useState(null); // Statut commande : "en_attente", "en_cours", "livr"
  const [showLoader, setShowLoader] = useState(false);

  const { socket, connected } = useContext(SocketContext);

  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const res = await getProducts(); // Rcupre depuis lAPI
        setProducts(res.data);
      } catch (err) {
        console.error("chec du chargement des produits", err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchProducts();
  }, []);

  useEffect(() => {
    if (!socket) return;

    socket.on('order:confirmedToClient', (data) => {
      if (data.productId === selectedProduct?.id) {
        setStatus(data.status);
      }
    });

    socket.on('order:readyForDriver', (data) => {
      if (data.productId === selectedProduct?.id) {
        setStatus('Prt  tre livr');
      }
    });

    return () => {
      socket.off('order:confirmedToClient');
      socket.off('order:readyForDriver');
    };
  }, [socket, selectedProduct]);

  const handleOrder = async (product) => {
    const token = localStorage.getItem('token');
    setShowLoader(true);
    setSelectedProduct(product);

    try {
      const res = await fetch('/api/orders', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          productId: product.id,
          productName: product.name,
          category: product.category,
          status: 'pending'
        })
      });

      const order = await res.json();
      socket.emit('order:request', order);
      setStatus(order.status);
    } catch (err) {
      alert("Impossible de passer la commande");
    } finally {
      setShowLoader(false);
    }
  };

  if (loading) {
    return <div>Chargement des produits...</div>;
  }

  return (
    <div className="p-6">
      <h2 className="text-2xl font-bold mb-4">Passer une commande</h2>
      
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
        {products.map(p => (
          <div key={p.id} className="border p-4 rounded shadow hover:shadow-lg transition-shadow duration-300">
            <h3 className="font-bold text-lg">{p.name}</h3>
            <p>{p.price} </p>
            <button
              disabled={showLoader}
              onClick={() => handleOrder(p)}
              className={`mt-2 btn-primary ${showLoader ? 'opacity-70 cursor-not-allowed' : ''}`}
            >
              {showLoader ? 'Envoi en cours...' : 'Commander'}
            </button>
          </div>
        ))}
      </div>

      {status && (
        <div className="mt-6 bg-blue-100 text-blue-800 p-4 rounded">
          Statut de votre commande : <strong>{status}</strong>
        </div>
      )}
    </div>
  );
}