import React, { useEffect, useState } from 'react';
import io from 'socket.io-client';

export default function TrackingPage({ orderId }) {
  const [status, setStatus] = useState('En attente');
  const [location, setLocation] = useState(null);
  const [map, setMap] = useState(null);

  const socket = io(process.env.REACT_APP_SOCKET_URL);

  // Charger la carte Google Maps
  useEffect(() => {
    // Crer une seule instance de socket
    const socket = io(process.env.REACT_APP_SOCKET_URL, {
      reconnection: true,
      reconnectionDelay: 1000,
      reconnectionDelayMax: 5000,
      reconnectionAttempts: 5
    });
    if (window.google && !map) {
      const googleMap = new window.google.maps.Map(document.getElementById('map'), {
        center: location || { lat: 48.8566, lng: 2.3522 },
        zoom: 12
      });
      setMap(googleMap);
    }

    // couter les mises  jour
    socket.on('order:confirmedToClient', (data) => {
      if (data._id === orderId) {
        setStatus(data.status);
        setLocation(data.restaurantLocation || data.driverLocation);
        map?.setCenter(location || { lat: 48.8566, lng: 2.3522 });
        new window.google.maps.Marker({
          position: location || { lat: 48.8566, lng: 2.3522 },
          map: map,
          title: "Livreur"
        });
      }
    });

    return () => {
      socket.off('order:confirmedToClient');
      socket.disconnect();
    };
  }, [map]);

  return (
    <div className="text-center py-12">
      <h2 className="text-2xl font-bold mb-4">Suivi de votre commande</h2>
      <p>Statut actuel : <strong>{status}</strong></p>

      <div id="map" style={{ height: '400px', width: '100%', marginTop: '20px' }}></div>
    </div>
  );
}