import React, { useEffect, useState, useContext } from 'react';
import { Bar, Pie } from 'react-chartjs-2';
import { AuthContext } from '../context/AuthContext';
import {
  getAdminUsers,
  getAdminOrders,
  getAdminOrderStats,
  getAdminDrivers
} from '../services/api';

export default function AdminDashboard() {
  const { token } = useContext(AuthContext);
  const [users, setUsers] = useState([]);
  const [orders, setOrders] = useState([]);
  const [drivers, setDrivers] = useState([]);
  const [orderStats, setOrderStats] = useState({ monthly: [], categories: {} });

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [userRes, orderRes, driverRes, statsRes] = await Promise.all([
          getAdminUsers(),
          getAdminOrders(),
          getAdminDrivers(),
          getAdminOrderStats()
        ]);
        setUsers(userRes.data);
        setOrders(orderRes.data);
        setDrivers(driverRes.data);
        setOrderStats(statsRes.data);
      } catch (err) {
        console.error("chec du chargement", err.message);
      }
    };
    if (token) fetchData();
  }, [token]);

  const orderMonthlyData = {
    labels: orderStats.monthly.map(m => m._id),
    datasets: [{
      label: 'Commandes par mois',
      data: orderStats.monthly.map(m => m.count),
      backgroundColor: 'rgba(75,192,192,0.4)',
      borderColor: 'rgba(75,192,192,1)',
      borderWidth: 1
    }]
  };

  const categoryData = {
    labels: Object.keys(orderStats.categories),
    datasets: [{
      label: 'Rpartition des catgories',
      data: Object.values(orderStats.categories),
      backgroundColor: ['#FF6384', '#36A2EB', '#FFCE56', '#4BC0C0', '#9966FF']
    }]
  };

  return (
    <div className="p-6 space-y-10">
      <h2 className="text-3xl font-bold mb-6">Tableau de bord Administrateur</h2>

      {/* Utilisateurs */}
      <section>
        <h3 className="text-xl font-semibold mb-2">Utilisateurs</h3>
        <table className="min-w-full bg-white shadow rounded overflow-hidden">
          <thead className="bg-gray-100">
            <tr><th>ID</th><th>Email</th><th>Rle</th></tr>
          </thead>
          <tbody>
            {users.map(user => (
              <tr key={user._id}><td>{user._id}</td><td>{user.email}</td><td>{user.role}</td></tr>
            ))}
          </tbody>
        </table>
      </section>

      {/* Graphiques */}
      <section>
        <h3 className="text-xl font-semibold mb-2">Commandes par mois</h3>
        <Bar data={orderMonthlyData} />
      </section>

      <section>
        <h3 className="text-xl font-semibold mb-2">Rpartition des catgories</h3>
        <Pie data={categoryData} options={{ responsive: true }} />
      </section>

      {/* Livreurs */}
      <section>
        <h3 className="text-xl font-semibold mb-2">Livreurs</h3>
        <table className="min-w-full bg-white shadow rounded overflow-hidden">
          <thead className="bg-gray-100">
            <tr><th>Nom</th><th>Livraisons</th></tr>
          </thead>
          <tbody>
            {drivers.map(driver => (
              <tr key={driver._id}>
                <td>{driver.name}</td>
                <td>{driver.deliveryCount || 0}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </section>
    </div>
  );
}