import React from 'react';

export default function Header({ user, onLogout }) {
  return (
    <header className="bg-amber-600 text-white shadow-md">
      <div className="container mx-auto px-4 py-4 flex justify-between items-center">
        <h1 className="text-xl md:text-2xl font-bold">FoodDelivery</h1>
        <nav>
          <ul className="flex space-x-4">
            <li><a href="#/" className="hover:underline">Accueil</a></li>
            {user && (
              <>
                <li><a href={`#${user.role}`} className="hover:underline capitalize">{user.role}</a></li>
                <li><button onClick={onLogout} className="text-sm hover:underline">Dconnexion</button></li>
              </>
            )}
          </ul>
        </nav>
      </div>
    </header>
  );
}