import axios from 'axios';

// Configuration de lAPI
const api = axios.create({
  baseURL: process.env.REACT_APP_API_URL || 'http://localhost:3001/api'
});

let setAuthToken = (token) => {
  if (token) {
    api.defaults.headers.common['Authorization'] = `Bearer ${token}`;
  } else {
    delete api.defaults.headers.common['Authorization'];
  }
};

const storedToken = localStorage.getItem('token');
if (storedToken) setAuthToken(storedToken);

// Services utilisateur
export const login = (email, password) =>
  api.post('/auth/login', { email, password });

export const register = (email, password, role) =>
  api.post('/auth/register', { email, password, role });

// Services produits
export const getProducts = () => api.get('/products');
export const createOrder = (order) => api.post('/orders', order);
export const updateOrderStatus = (id, status) => api.put(`/orders/${id}`, status);

// Services admin
export const getAdminUsers = () => api.get('/admin/users');
export const getAdminOrders = () => api.get('/admin/orders');
export const getAdminOrderStats = () => api.get('/admin/order-stats');
export const getAdminDrivers = () => api.get('/admin/drivers');

// WebSocket
export const connectSocket = () => io(process.env.REACT_APP_SOCKET_URL || 'http://localhost:3001');

export default api;