import React, { useEffect, useState } from 'react';
import io from 'socket.io-client';

export default function RestaurantDashboard() {
  const [orders, setOrders] = useState([]);

  const socket = io('https://your-backend-url.onrender.com'); 

  useEffect(() => {
    socket.on('order:requested', (order) => {
      setOrders(prev => [...prev, order]);
    });
  }, []);

  const acceptOrder = async (order) => {
    const token = localStorage.getItem('token');
    await fetch(`/api/orders/${order.productId}`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      },
      body: JSON.stringify({ status: 'accepted_by_restaurant' })
    });

    socket.emit('order:acceptedByRestaurant', { ...order, status: 'accepted_by_restaurant' });
    setOrders(orders.filter(o => o.productId !== order.productId));
  };

  return (
    <div className="p-6">
      <h2 className="text-2xl font-bold mb-4">Vos nouvelles commandes</h2>
      <ul className="space-y-4">
        {orders.map(order => (
          <li key={order.productId} className="border p-4 rounded shadow flex justify-between items-center">
            <span>Commande #{order.productId} - {order.productName}</span>
            <button onClick={() => acceptOrder(order)} className="btn-success">
              Accepter
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
}