import React, { useState } from 'react';
import { register as apiRegister } from '../services/api';

export default function Register({ onLogin }) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [role, setRole] = useState('client');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      const res = await apiRegister(email, password, role);
      localStorage.setItem('token', res.data.token);
      onLogin(jwtDecode(res.data.token));
    } catch (err) {
      setError("chec dinscription");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-md mx-auto bg-white p-6 rounded shadow mt-8">
      <h2 className="text-2xl font-bold mb-4">Inscription</h2>
      {error && <p className="text-red-500">{error}</p>}
      <form onSubmit={handleSubmit}>
        <input
          type="email"
          placeholder="Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
          disabled={loading}
          className="w-full border rounded px-3 py-2 mb-4"
        />
        <input
          type="password"
          placeholder="Mot de passe"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
          disabled={loading}
          className="w-full border rounded px-3 py-2 mb-4"
        />
        <select
          value={role}
          onChange={(e) => setRole(e.target.value)}
          className="w-full border rounded px-3 py-2 mb-4"
        >
          <option value="client">Client</option>
          <option value="restaurant">Restaurant</option>
          <option value="driver">Livreur</option>
          <option value="admin">Administrateur</option>
        </select>
        <button type="submit" disabled={loading} className="btn-success w-full">
          {loading ? "Cration en cours..." : "Sinscrire"}
        </button>
      </form>
    </div>
  );
}